.todos {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 55px;
  background-color: rgb(245, 245, 245);
}

.todos-wrapper {
  position: relative;
  width: 550px;
  max-width: 90%;
  background: #fff;
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03),
    0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.todos-header {
  margin: 0 0 15px;
  color: rgba(175, 47, 47, 0.15);
  font-size: 100px;
  font-weight: 100;
}

.todos-input {
  width: 100%;
  padding: 16px 16px 16px 60px;
  border: none;
  background-color: rgba(0, 0, 0, 0.003);
  font-size: 24px;
  line-height: 1.4em;
}

.todos-input:focus {
  outline: none;
}

.todos-input::placeholder {
  font-style: italic;
  color: rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 768px) {
  .todos-input {
    font-size: 18px;
  }
}

.todos-toggle-all {
  position: absolute;
  top: 50%;
  left: 14px;
  font-size: 22px;
  color: #e6e6e6;
  border: none;
  outline: none;
  background: none;
  transform-origin: 50%;
  transform: translateY(-50%) rotate(90deg);
  cursor: pointer;
  transition: color 0.4s;
}

.todos-toggle-all--checked {
  color: #737373;
}

.todos-list__container {
  margin-top: -1px;
  width: 100%;
  border-top: 1px solid #e6e6e6;
}

.todos-list {
  position: relative;
  z-index: 2;
  padding: 0;
  margin: 0;
  list-style-type: none;
  background-color: white;
  color: #4d4d4d;
  font-size: 24px;
}

.todos-list-item {
  position: relative;
  width: 100%;
  padding: 16px 16px 16px 60px;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #ededed;
}

.todos-list-item__description {
  display: block;
  word-break: break-all;
  transition: all 0.4s;
}

.todos-list-item__description--completed {
  color: #d9d9d9;
  text-decoration: line-through;
}

.todos-list-item__description:before {
  content: "";
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 32px;
  height: 32px;
  border: 2px solid #d1d7dc;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
  background-position: 4px 5px;
  border-radius: 50%;
  opacity: 0.4;
  transition: opacity 0.4s;
}

.todos-list-item__description--editing:before {
  display: none;
}

.todos-list-item__toggle {
  height: 38px;
  width: 38px;
  position: absolute;
  z-index: 5;
  top: 10px;
  left: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
}

.todos-list-item__toggle:focus {
  outline: none;
}

.todos-list-item__toggle:checked ~ .todos-list-item__description:before {
  background-color: #54e0c7;
  border-color: #54e0c7;
  opacity: 1;
}

.todos-list-item__edit {
  z-index: 10;
  position: absolute;
  top: 0;
  left: calc(7% + 5px);
  width: calc(93% - 6px);
  height: calc(100% + 2px);
  padding: 6px;
  border: 1px solid #999;
  outline: none;
  box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
  font-size: 24px;
  line-height: 1.4em;
}

.todos-list-item:hover .todos-list-item__delete {
  display: block;
}

.todos-list-item__delete {
  display: none;
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border: none;
  outline: none;
  background: none;
  color: #cc9a9a;
  font-size: 30px;
  cursor: pointer;
  transition: color 0.2s;
}

.todos-list-item__delete:hover {
  color: #af5b5e;
}

.todos-list-item__delete:after {
  content: "Ã—";
}

.todos-footer {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  color: #777777;
  height: 50px;
  border-top: 1px solid #e6e6e6;
  font-size: 14px;
}

.todos-footer:before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.todos-footer-count,
.todos-footer-clear {
  flex: 0 1 25%;
  font-size: inherit;
}

.todos-footer-clear {
  position: relative;
  z-index: 10;
  background-color: transparent;
  color: inherit;
  border: none;
  outline: none;
  text-align: right;
  cursor: pointer;
}

.todos-footer-clear:hover {
  text-decoration: underline;
}

.todos-footer-filters {
  flex: 0 1 50%;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todos-footer-button {
  position: relative;
  z-index: 10;
  padding: 3px 7px;
  margin: 3px;
  border-radius: 3px;
  background-color: transparent;
  border: 1px solid transparent;
  outline: none;
  color: inherit;
  font-size: inherit;
  cursor: pointer;
}

.todos-footer-button:hover {
  border-color: rgba(175, 47, 47, 0.1);
}

.todos-footer-button.selected {
  border-color: rgba(175, 47, 47, 0.2);
}

.info {
  margin: 65px auto 0px;
  color: rgb(191, 191, 191);
  font-size: 10px;
  text-shadow: rgba(255, 255, 255, 0.5) 0px 1px 0px;
  text-align: center;
}

.info a {
  color: rgba(0, 0, 0, 0.5);
}